
<style>

  /*body{background-color: red;}*/

  /*div{width: 300px;height: 200px;color: #fff;}*/

  /*div.rgba{background-color: rgba(0,0,0,0.7);}*/

  /*div.opacity{background-color: #000;filter:alpha(opacity=70);-moz-opacity:0.70;-khtml-opacity:0.7;opacity: 0.7;margin-top: 20px;}*/

  /*filter:alpha(opacity=30);-moz-opacity:0.30;-khtml-opacity:0.7;opacity: 0.7;*/
  .container-full{

    width:100%;
    height: 100%;
    position: relative;

  }

  .container-fluid{
    padding-right: 0px;
    padding-left: 0px;

    position: fixed;

    padding-top: 56px;
    height: 100%;
  }

  .footer{
    position:fixed;bottom:0;z-index:999;
  }


  .nav-left::-webkit-scrollbar {/*滚动条整体样式*/
    width: 4px;     /*高宽分别对应横竖滚动条的尺寸*/
    height: 4px;
  }
  .nav-left::-webkit-scrollbar-thumb {/*滚动条里面小方块*/
    border-radius: 5px;
    -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
    background: rgba(0,0,0,0.2);
  }
  .nav-left::-webkit-scrollbar-track {/*滚动条里面轨道*/
    -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
    border-radius: 0;
    background: rgba(0,0,0,0.1);
  }
  iframe{width:100%;height:100%;border:0}

  ul li img {width:18px;height:18px;text-align:left;margin-left: -10px;}

  div.popContainer{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    background: rgba(0,0,0,0.3);
    display: none;
  }

  div.popMenu{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    background: rgba(0,0,0,0.1);
    display: none;
  }

</style>

<template>

<div class="container-full">
  <nav class="navbar navbar-expand-sm bg-dark navbar-dark fixed-top" style="padding-left: 5px;padding-right: 5px;"  >
    <button type="button" class="btn btn-info " @click="NavLeftShowOrNot(0)"  data-toggle="戳这里你会很爽"
            title="戳这里你会很爽" style="padding-left: 5px;padding-right: 5px;">链接力LP</button>

      <SearchInput ref="searchInput"></SearchInput>

    <div :class="dataParams.RightMenuClass" id="collapsibleNavbar"  style="width:300px;">
      <ul class="navbar-nav">

        <li class="nav-item" v-for="(item,index) in dataParams.NavArr "    :key="index"   >
          <a class="nav-link" :class="item.cate_index==dataParams.SearchCate? 'nav-link active ' : 'nav-link'" href="#" @click="CateClick($event,item.cate_index)" >{{item.cate_name}}</a>
        </li>




        <li class="nav-item">
          <a class="nav-link" href="#" data-toggle="modal" data-target="#layer" >设置</a>
        </li>
        <li class="nav-item">
          <a class="nav-link" href="#" @click="NavLeftShowOrNot(0);PopMenuHideClick();">{{dataParams.navLeftBt}}</a>
        </li>
      </ul>
    </div>

    <button class="navbar-toggler" type="button"  @click="PopMenuClick()"  style="padding-left: 5px;padding-right: 5px;" >
      <span class="navbar-toggler-icon"></span>
    </button>
  </nav>

  <div class="container-fluid" >


      <div class="nav-left" :style="{width:dataParams.navLeft}" style="background-color:#eeeeee;
      filter:alpha(opacity=50);-moz-opacity:0.50;-khtml-opacity:0.5;opacity: 0.8;
      position:absolute;float:left;width:130px;height: 100%;padding-bottom:100px;overflow-y: scroll;overflow-x: hidden;z-index: 8999;"   >

        <ul class="nav nav-pills flex-column ">

        <li class="nav-item" v-for="(item,index) in dataParams.MenuArr "    :key="index"   @click="SearchClick(null,item.site_index)"  :data-toggle="item.site_name"
            :title="item.site_name" style="white-space:nowrap;">

          <a  class="navbar-dark" :class="item.site_index==dataParams.site_index? 'nav-link disabled ' : 'nav-link'"   href="#" style="text-align: left" :data-toggle="item.site_name"
              :title="item.site_name">
            <img :src="item.site_ico" :alt="item.site_name"   />&nbsp;&nbsp;{{item.site_name}}</a>
        </li>

        </ul>
        <hr class="d-sm-none">
      </div>


    <div   class="nav-left" :style="{'padding-left':dataParams.ConMarginLeft}" style="-webkit-overflow-scrolling:touch;width:100%;height: 100%;padding-bottom:20px;overflow-y: scroll;">
            <iframe id="engine" name="engine" v-bind:src="dataParams.SearchSite"
            frameborder="0" marginwidth="0" marginheight="0" allowtransparency="true" style="height: 100%; width: 100%;background-color:#fff"></iframe>
    </div>

  </div>


  <nav class="bg-light fixed-bottom" style="width:100%;height: 28px;vertical-align: bottom;background-color: #eeeeee;align-content: center;color: #848B99;font:12px arial">
    <div style="padding-top:10px; ">Copyright© 2020-2048 想链接请发邮件到<a href="mailto:realsodo@hotmail.com" >realsodo@hotmail.com</a></div>

  </nav>

  <div role="dialog" class="modal fade" id="layer" >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">

          <h4 class="modal-title">设置</h4>
          <button type="button" class="close" data-dismiss="modal">
            <span>&times;</span>
          </button>
        </div>

          <a class="nav-link" href="#" @click="SetIndex(0)">设置为启动页</a>


          <a class="nav-link" href="#" @click="ClsIndex(0)">清空启动页</a>

<!--        <div class="modal-body text-right">-->
<!--          <button class="btn btn-primary btn-sm" data-dismiss="modal">取消</button>-->
<!--          <button class="btn btn-danger btn-sm" v-on:click="deletemsg(nowIndex)" data-dismiss="modal">确认</button>-->
<!--        </div>-->
      </div>
    </div>
  </div>




  <div class='popContainer'><div style="widht:200px;height:60px;margin: auto;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;color: #eeeeee;">努力加载中，休息去喝个水吧</div></div>

  <div class='popMenu' @click="PopMenuHideClick" @scroll="PopMenuHideClick" @touchstart="PopMenuHideClick" @keydown="PopMenuHideClick" ></div>

</div>



</template>


<script>


  import SearchInput from '@/components/SearchInputEx'

  //import dropdown from '@/components/dropdown'
  //import $ from 'jquery'

  import baseData from '../assets/basedata'

    export default {
      name: "SearchComponent",
      data () {

        return {
          timer:'',
          itemlist:[],
          nodatatext:'',
          placeholder:'search',
          LeftImg:'95px',
          dataParams: {
            RightMenuClass:'collapse navbar-collapse',
            searchText: '',
            SearchSite:'',
            SearchCate:0,
            site_index:0,
            MenuArr: [] ,
            NavArr:baseData.cate,
            ConMarginLeft:"0px",
            navLeft:"138px",
            navLeftBt:"隐藏导航菜单"
          }
        }
      },
      components: {SearchInput},
      beforeDestroy() {
        clearTimeout(this.timer);
      },
      mounted(){

        // window.addEventListener('message',function(event){
        //   if(event.data.refresh){
        //     //此处执行事件
        //     console.log(111);
        //   }
        // });

        this.loadDataInit();
        this.NavLeftInit();

        this.HideProcess(".popContainer",1);

        window['HideProcess'] = (argDiv,argClearTime) => {
          this.HideProcess(argDiv,argClearTime);
        };


        window['addEvent'] = (elem, type, showAll) => {
          this.addEvent(elem, type, showAll);
        };

        let IframeOnClick = {
          resolution: 200,
          iframes: [],
          interval: null,
          Iframe: function() {
            this.element = arguments[0];
            this.cb = arguments[1];

            this.hasTracked = false;
          },
          track: function(element, cb) {
            this.iframes.push(new this.Iframe(element, cb));
            if (!this.interval) {
              let _this = this;
              this.interval = setInterval(function() { _this.checkClick(); }, this.resolution);
            }
          },

          checkClick: function() {


            //console.log(this.iframes[0].element.contentWindow.screenTop);


            if (document.activeElement) {
              let activeElement = document.activeElement;

              for (let i in this.iframes) {

                if (activeElement === this.iframes[i].element) { // user is in this Iframe

                  if (this.iframes[i].hasTracked == false) {
                    this.iframes[i].cb.apply(window, []);

                    this.iframes[i].hasTracked = true;
                  }
                } else {
                  this.iframes[i].hasTracked = false;
                }
              }
            }
          }
        };




        this.$nextTick(() =>{


          try{
            const iframe = document.querySelector('#engine');


            // // 监听点击事件
            // IframeOnClick.track(iframe, function() {
            //   //做你想做的事情
            //   PopMenuHideClick();
            //
            // });

            if(iframe.attachEvent){
                iframe.attachEvent("onload", function(){
                //console.log(iframe.contentWindow.document.body.innerHTML);
                //document.getElementById("iframeInner").innerHTML=iframe.contentWindow.document.body.innerHTML;
                HideProcess(".popContainer",1);

                return;})



            }else{
                iframe.onload=iframe.onreadystatechange = function(){
                  //console.log(iframe.contentDocument.body.innerHTML);
                  //document.getElementById("iframeInner").innerHTML=iframe.contentDocument.body.innerHTML;
                  HideProcess(".popContainer",1);
                  //window.parent.postMessage({refresh:'id'},'*');
                  return;
                };


                }

              }catch(e){
                HideProcess(".popContainer",1);
                console.warn('setIframeHeight Error');
              }

          // if (iframe.attachEvent) {
          //   iframe.attachEvent("onload", function() {
          //
          //     HideProcess();
          //   });
          // } else {
          //   iframe.onload = function() {
          //     HideProcess();
          //   };
          // }
        });


      },

      methods:{
        addEvent:function (elem, type, showAll) {
          type = type.split(' ');
          type.forEach(function(item) {
            elem.addEventListener(item, function(ev) {
              console.log(showAll ? ev : ev.type);
            });
          });
        },
        loadDataInit:function(){
          //加载数据
          let searchText=localStorage.getItem('searchText');
          if (searchText!=null)
            this.dataParams.searchText=searchText;


          //有首页设置加载首页设置
          let setIndex=localStorage.getItem('setIndex');
          if (setIndex!=null){
            let site = setIndex ? JSON.parse(setIndex) :{};
            this.dataParams.site_index=site.site_index;
            this.dataParams.SearchCate=site.site_cate;
          }else{

            let site_index=localStorage.getItem('site_index');
            if (site_index!=null)
              this.dataParams.site_index=site_index;

            let site=baseData.data.filter(item=>item.site_index== this.dataParams.site_index)[0];
            this.dataParams.SearchCate=site.site_cate;
          }


          //分类绑定数据
          this.dataParams.MenuArr=this.filterNav(baseData.data);

        },
        filterNav(argBaseData){
          let ret;// 区分移动和pc网站  -1  是移动  1 是pc  0 pc移动显示
          if (this._isMobile())
            ret = argBaseData.filter(item=>item.site_show<=0 && item.site_cate==this.dataParams.SearchCate);
          else
            ret = argBaseData.filter(item=>item.site_show>=0 && item.site_cate==this.dataParams.SearchCate);
          return ret;
        },
        NavLeftInit:function(){

          let navLeft=localStorage.getItem('navLeft');

          if  (navLeft==null)
            $(".navbar-brand").tooltip('show');
          else
            $(".navbar-brand").tooltip('hide');


          if( this._isMobile() && navLeft==null){
            //第一次移动端加载
            this.dataParams.navLeft='0px';
            this.dataParams.navLeftBt='显示导航菜单';
          }else if(navLeft==null){
            //第一次pc端加载
            this.dataParams.navLeft='138px';
            this.dataParams.navLeftBt='隐藏导航菜单';
          }else{
            //正常加载
            this.dataParams.navLeft=navLeft;
            if (this.dataParams.navLeft=='0px'){
              this.dataParams.navLeftBt='显示导航菜单';
            }else{
              this.dataParams.navLeftBt='隐藏导航菜单';
            }
          }

          if (!this._isMobile())
            this.dataParams.ConMarginLeft=this.dataParams.navLeft;
          else
            this.dataParams.ConMarginLeft="0px";

          //加载页面
          this.SearchClick(null,-2);

        },
        NavLeftShowOrNot:function (show) {
          if(show==1)this.dataParams.navLeft='0px';//显示

          if(show==-1)this.dataParams.navLeft='138px';//隐藏

          //0 是和前置状态切换

          if (this.dataParams.navLeft=='138px'){
            this.dataParams.navLeft='0px';
            this.dataParams.navLeftBt='显示导航菜单';

          }else if(this.dataParams.navLeft=='0px'){
            this.dataParams.navLeft='35px';
            this.dataParams.navLeftBt='显示导航菜单';
          }
          else{
            this.dataParams.navLeft='138px';
            this.dataParams.navLeftBt='隐藏导航菜单';
          }

          if (!this._isMobile())
            this.dataParams.ConMarginLeft=this.dataParams.navLeft;
          else
            this.dataParams.ConMarginLeft="0px";

          localStorage.setItem('navLeft',this.dataParams.navLeft);
          this.PopMenuHideClick();

        },NavLeftSearch:function () {

          if (this._isMobile()) {
            this.dataParams.navLeft = '35px';
            this.dataParams.navLeftBt = '显示导航菜单';
          }
          localStorage.setItem('navLeft',this.dataParams.navLeft)


        },
        CateClick:function(e,value){

          this.PopMenuHideClick();
          this.NavLeftShowOrNot(1);
          this.dataParams.SearchCate=value;
          this.dataParams.MenuArr=this.filterNav(baseData.data);
          this.dataParams.site_index=this.dataParams.MenuArr[0].site_index;



        },
        PopMenuClick:function(){

          if(this.dataParams.RightMenuClass=="collapse navbar-collapse"){
            this.dataParams.RightMenuClass="navbar-collapse collapse show";
            this.ShowProcess(".popMenu",0);
            this.$refs.searchInput.MobileSet.LeftImg="150px";
            //this.LeftImg="150px";
            ///$('.navbar-toggler').click();
          }else{
            this.dataParams.RightMenuClass="collapse navbar-collapse";
            this.HideProcess(".popMenu",0);
            this.$refs.searchInput.MobileSet.LeftImg="95px";
          }

        },
        PopMenuHideClick:function(){
          this.dataParams.RightMenuClass="collapse navbar-collapse";
          this.HideProcess(".popMenu",0);
          this.$refs.searchInput.MobileSet.LeftImg="95px";
        },
        HideProcess:function(argDiv,argTimeClear){
          $(argDiv).hide();
          if (argTimeClear>0){clearTimeout(this.timer);}

        },
        ShowProcess:function(argDiv,argTimeOut){
          $(argDiv).show();
          if (argTimeOut>0){
            this.timer=setTimeout(function () {
              this.HideProcess(argDiv,argTimeOut)
            }, argTimeOut);
          }

        }
        ,
        SearchClick:function (argContent,site_index) {


          let tmpSiteIndexPre=this.dataParams.site_index;

          if (site_index>=0){
            this.dataParams.site_index=site_index;
          }

          let site=this.dataParams.MenuArr.filter(item=>item.site_index== this.dataParams.site_index)[0];

          if ( (this.dataParams.searchText!=argContent   || site_index>=0) && site.site_mode!="1" && tmpSiteIndexPre!=site_index){
            this.ShowProcess(".popContainer",10000);
          }

          if (argContent!=null){
            this.dataParams.searchText=argContent;
          }

          let _url = site.site_url;
          if (this._isMobile())
              _url = site.site_m;


          _url  = _url.replace(/{query_word_placeholder}/g, this.dataParams.searchText);
          if (site.site_mode=="1")
          {
              window.open(_url);
          }else if  (site.site_mode=="-1"){


              this.dataParams.SearchSite=_url;

              localStorage.setItem('site_index',this.dataParams.site_index)
              //if (this._isMobile()){this.NavLeftShowOrNot(-1);}


          }
          else{


              this.dataParams.SearchSite=_url;
              localStorage.setItem('site_index',this.dataParams.site_index)
              //this.NavLeftSearch();
          }
          this.PopMenuHideClick();

        },
        SetIndex:function(index){

          let value= this.dataParams.MenuArr.filter(item=>item.site_index== this.dataParams.site_index)[0];
          if (value.site_mode=="1")
          {
            alert("弹出页面不能设置成启动页面");
          }
          else{
            localStorage.setItem('setIndex', JSON.stringify(value));
          }
          this.PopMenuHideClick();


      },
        ClsIndex:function(index){
          localStorage.removeItem('setIndex');
          this.PopMenuHideClick();
        },
        _isMobile() {
          let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
          return flag;
        }
        }
    }


</script>




