<style type="text/css">
  .gray{
    background-color: #eee;
  }
  .listyle{
    font-size: 16px;
    line-height: 35px;
    padding-left: 16px;
  }
  .ulstyle{
    border:1px solid #ccc;
    border-top: none;
  }
</style>
<template>
  <div class="container" >
    <div class="input-group">
      <input type="text" class="form-control input-lg" placeholder="请输入关键字" v-model="txt" @keyup="get($event)" @keydown.down="changeDown()" @keydown.up="changeUp()">
      <span class="input-group-btn">
        <button class="btn btn-default btn-lg" type="button" @click="getMsg()">搜索</button>
      </span>
    </div>
    <ul class="list-unstyled ulstyle" v-show="myData.length!=0">
      <li v-for="(item,$index) in myData"   :class="{gray:$index==nowIndex,listyle:true}"   @mouseover="mouseOver($index)" @click="getMsg()">{{item}}
      </li>
    </ul>
  </div>
</template>

<script>
    export default {
        name: "SearchInputBt",
        data(){
          return {
          txt:"11",
            nowIndex:-1,
          myData:[
            "1","2","3"
          ]
          }
        },
      methods:{
        get:function(event){
          if(event.keyCode==38 || event.keyCode==40){
            return;
          }
          if(event.keyCode==13){
            window.open("https://www.baidu.com/s?wd="+this.txt);
            this.txt="";
          }
          this.$http.jsonp("https://sp0.baidu.com/5a1Fazu8AA54nxGko9WTAnF6hhy/su",{
            wd:this.txt
          },{
            jsonp:"cb"
          }).then(function(res){
            this.myData=res.data.s
          },function(res){
            alert(res.status);
          });
        },
        changeDown:function(){
          this.nowIndex++;
          if(this.nowIndex==this.myData.length){
            this.nowIndex=0;
            this.txt=this.myData[0];
          }else{
            this.txt=this.myData[this.nowIndex];
          }
        },
        changeUp:function(){
          this.nowIndex--;
          if(this.nowIndex==-1){
            this.nowIndex=this.myData.length-1;
            this.txt=this.myData[this.nowIndex];
          }else{
            this.txt=this.myData[this.nowIndex];
          }
        },
        mouseOver:function(n){
          this.nowIndex=n;
          this.txt=this.myData[this.nowIndex];
        },
        getMsg:function(){
          window.open("https://www.baidu.com/s?wd="+this.txt);
          this.txt="";
        }
      }
    }
</script>

<style scoped>

</style>



