import Vue from 'vue'
import Router from 'vue-router'

import SearchComponent from '@/components/SearchComponent'
import SearchInputBt from "../components/SearchInputBt";

Vue.use(Router)

export default new Router({
  //mode:'history',
  //base: '/vue-demo/dist/', //如果项目根目录不为域名，则添加该行
  routes: [
    {
      path: '/',
      name: 'SearchComponent',
      component: SearchComponent
    },  {
      path: '/SearchInputBt',
      name: 'SearchInputBt',
      component: SearchInputBt
    }
  ]
})



